a {
	color: var(--prim-2);
}

div.divider {
	display: flex;
	align-items: center;
	text-align: center;
	margin: 16px 0;
	color: var(--general-4);
	font-size: 0.75rem;

	&::before,
	&::after {
		content: '';
		flex: 1;
		border-bottom: 1px solid var(--general-2);
	}

	&:not(:empty)::before {
		margin-right: 16px;
	}

	&:not(:empty)::after {
		margin-left: 16px;
	}
}

button.btn-google {
	border: 1px solid var(--neutral-4);
	border-radius: 4px;
	height: 40px;
	box-shadow: 0px 1px 4px rgba(83, 86, 91, 0.1);
}
